import { t } from '../../../helpers/i18n/dictionary';
import useHomeAttributes from '../../../hooks/profiles/useHomeAttributes';
import AttributeGroup from '../../Attribute/AttributeGroup/AttributeGroup';
import PageMeta from '../../Layout/PageMeta/PageMeta';

const Website = () => {
  const { data } = useHomeAttributes();

  if (!data) {
    return <></>;
  }

  return (
    <section>
      <PageMeta title={t('Website Configuration')} />
      <AttributeGroup attributes={data.home} groupTitle={t('Home')} />
      <AttributeGroup attributes={data.theme} groupTitle={t('Theme')} />
    </section>
  );
};

export default Website;
